/*--------------------------------------------------------------
# Review
--------------------------------------------------------------*/

/**
 * Single donor with user image, details, and review text.
 *
 * 1. Header wraps the user photo and details. It displays above review text.
 * 2. Image is the image of the donor.
 * 3. Details include the donor's name, amount given, and date of the donation.
 */

/*--------------------------------------------------------------
# Spacing
--------------------------------------------------------------*/

$donor-space: 20px;
$donor-color: #333;
$donor-space-eighth: $donor-space * 0.125;
$donor-space-quarter: $donor-space * 0.25;
$donor-space-fifth: $donor-space * 0.2;
$donor-space-half: $donor-space * 0.75;
$donor-space-three-quarters: $donor-space * 0.75;
$donor-space-and-quarter: $donor-space * 1.25;
$donor-space-and-half: $donor-space * 1.5;
$donor-space-and-three-quarters: $donor-space * 1.75;
$donor-space-double: $donor-space * 2;
$donor-space-triple: $donor-space * 3;

.give-donor {
	border: 1px solid $give-card-border;
	background: #fff;
	color: $donor-color;
	font-size: 1em;
	margin-bottom: $donor-space;
	padding: $donor-space;
	position: relative;
	transition: all 0.2s ease-out;
	width: 100%;

	&__header {

		/* [1] */
		align-items: center;
		display: flex;
	}

	&__image {

		/* [2] */
		display: flex;
		align-items: center;
		justify-content: center;

		background: #fff;
		margin-right: $donor-space-half;
		overflow: hidden;
		border-radius: 50%;
		flex-basis: 40%;

		position: relative;

		img {
			object-fit: cover;
			width: 100%;
		}

		.give-donor__name_initial {
			position: absolute;
		}

		&[data-anonymous-donation='0'] img[src*='anonymous-user.svg'],
		&[data-anonymous-donation='1'] .give-donor__name_initial {
			visibility: hidden;
		}

		&[data-anonymous-donation='0'][data-has-valid-gravatar='0'] {
			border: 1px solid $give-card-border;
		}
	}

	&__details {

		/* [3] */
		flex-grow: 1;
		overflow: hidden;
		flex-basis: 60%;
	}

	&__name {
		font-size: 1em !important;
		font-weight: bold !important;
		color: $donor-color !important;
		line-height: 1.3 !important;
		margin: 0 !important;
		padding: 0 !important;
	}

	&__timestamp {
		color: $donor-color !important;
		display: block;
		font-size: 75% !important;
		line-height: 1.3 !important;
	}

	&__content {
		margin-top: $donor-space-half;
		width: 100%;

		p {
			color: $donor-color;
			font-size: 1em !important;
			font-size: 1em !important;
			line-height: 1.5 !important;
			margin: 0 0 $donor-space !important;
			padding: 0 !important;
			width: 100%;
		}

		&:first-child {
			margin-top: 0 !important;
		}

		& :last-child {
			margin-bottom: 0 !important;
		}
	}

	&__read-more {
		cursor: pointer;
		white-space: nowrap;
	}

	&__excerpt + &__comment {
		display: none;
	}
}

.give-donor-wall-modal {
	p:last-child {
		margin-bottom: 0;
	}
}
